import React from 'react';
import BlogPost from './blog-post';


class Blog extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            error: null,
            isLoaded: false,
            items: {}
        };
    }
  
    componentDidMount() {
        fetch( "http://diegoescobar.ca/wp-json/wp/v2/posts" )
            .then(response => response.json())
            .then(
                (result) => {
                    const data = Object.entries(result);
                    console.log(data);
                    this.setState({
                        isLoaded: true,
                        items: data
                    });
                },
                (error) => {
                    this.setState({
                        isLoaded: true,
                        error
                    }
                );
            }
        )
    }
  
    render() {
        const { error, isLoaded, items } = this.state;

        if (error) {
            return (<div>Error: {error.message}</div>);
        } else if (!isLoaded) {
            return (<div>Loading...</div>);
        } else {
            return (
            <div>
                <h3>Blog</h3> 
                    {items.map((item, i)=>(

                        <BlogPost key={i}
                            title={item[1].title[0]} 
                            content={item[1].content.rendered} 
                        />
                    ))}
            </div>
        );
      }
    }
  }

export default Blog;