import React from 'react';

class BlogPost extends React.Component {
    constructor(props){
        super(props);
        this.state = {};
    }

    render(){
        return(
            <div className="blog post">
                <h2>{this.props.title}</h2>
                <p dangerouslySetInnerHTML={{__html: this.props.content}}></p>
            </div>
        )
    }
}

export default BlogPost;